function createClient() {
  return createAuth0Client({
    domain: AUTH0_DOMAIN,
    clientId: AUTH0_CLIENT_ID,

    authorizationParams: {
      redirect_uri:
        absoluteURL(base_url) + "?redirect-to=" + window.location.href,
    },
  });
}

async function handleRedirects(auth0) {
  if (
    location.search.includes("state=") &&
    (location.search.includes("code=") || location.search.includes("error="))
  ) {
    await auth0.handleRedirectCallback();

    if (location.search.includes("redirect-to")) {
      const params = new URLSearchParams(location.search);
      const redirectTo = params.get("redirect-to");
      window.location.replace(redirectTo);
    } else {
      window.history.replaceState({}, document.title, "/");
    }
  }
}

function setupLoginLogout(user, auth0) {
  const userMenu = document.getElementById("user-menu");
  userMenu.innerHTML = "";
  const button = createElementWithClasses("button", ["ah-button"], userMenu);
  if (user) {
    setupLogout(button, auth0);
  } else {
    setupLogin(button, auth0);
  }
}

function setupLogin(loginBtn, auth0) {
  loginBtn.textContent = "Login";
  loginBtn.addEventListener("click", (e) => {
    e.preventDefault();
    auth0.loginWithRedirect();
  });
}

function setupLogout(logoutBtn, auth0) {
  logoutBtn.textContent = "Logout";
  logoutBtn.addEventListener("click", (e) => {
    e.preventDefault();
    localStorage.removeItem(USER_DATA_KEY);
    auth0.logout();
  });
}
